.toolbar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  z-index: 1;
}
.toolbar .toolbar-button {
  display: inline-flex;
  width: 75px;
  height: 20px;
  border-radius: 20px;
  margin: 5px;
  line-height: 20px;
  text-align: center;
  justify-content: center;
  background: #333;
  color: #fff;
  font-size: 0.75em;
  cursor: pointer;
}
.toolbar .toolbar-button.active {
  background: #03a9f4;
  font-size: 0.8em;
  font-weight: 700;
}
.project-links {
  display: flex;
  padding: 0 15px;
}
.project-link {
  background: #000;
  padding: 5px 10px;
  font-weight: 700;
  width: fit-content;
  border-radius: 5px;
  border: 2px solid white;
  text-align: center;
}

.action-text {
  padding-left: 10px;
}
